.body{
    
}

.projects{
    width: 80%;
    margin: 50px auto 50px auto;
}
.projects h1{
    text-align: center;
}
.project-list{
    margin-top: 25px;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    list-style: none;
    grid-auto-rows: minmax(500px, auto);
}

.project-list li{
    margin: 0px auto 0px auto;
    border-radius: max(0px, min(8px, calc((100vw - 4px - 100%) * 9999))) / 8px;
    max-width: 380px;
}

.project-list li img{
    margin-top: 20px;
    vertical-align: middle;
    max-width: 370px;
    max-height: 350px;
    height: 48%;
}

.project-list li .image img {
    display: block;
    width: 100%;
    transition: transform .2s;
    margin-left: auto;
    margin-right: auto;
}

.project-list li img:hover {
    transform: scale(1.1);
}

.inner{
    margin: auto;
    display: block;
    padding-top: 15px;
    height: 52%;
    max-width: 400px;
}

.inner p{
    margin: 4px 4px 4px 4px;
    text-align: center;
}

.inner h4, .inner h5{
    text-align: center;
}

.test{
    inline-size: 150px;
    overflow-wrap: break-word;
}


@media (max-width: 797px) {
    .projects{
        margin-top: 120px;
    }
}